import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout_v2 from "@components/layout_v2";
import Seo from "@components/seo";
import { useLocation } from "@reach/router";
import HireGameUiDesignerIndex from "@screens/hiringPages/hire-game-ui-designer";
import BreadCrumbV2 from "@components/common/breadcrumb-v2";

const HireGameUiDesigner = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Hire Game UI Designer", link: null },
  ];
  const { data } = props;
  const location = useLocation();
  const fileUrl =
    data?.hireGameUiDesignerImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;
  const validData = data?.allMetaJson?.nodes[0];

  return (
    <Layout_v2 db={db}>
      <Seo
        title={validData?.title}
        metaTitle={validData?.metaTitle}
        description={validData?.description}
        image={fileUrl}
        siteFirestore={data?.site?.siteMetadata?.firestore}
        slug={location.pathname.split("/")?.[1]}
      ></Seo>
      <BreadCrumbV2 items={breadcrumbLinks} />
      <main>
        <HireGameUiDesignerIndex db={db} />
      </main>
    </Layout_v2>
  );
};

export default HireGameUiDesigner;
export const queryData = graphql`
  query MyQuery {
    hireGameUiDesignerImg: file(
      relativePath: { eq: "hire-game-ui-designer.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allMetaJson(filter: { slug: { eq: "hire-game-ui-designer" } }) {
      nodes {
        metaTitle
        title
        slug
        description
      }
    }
  }
`;
